<template>
    <div>
        <tab-widget :tabs="tabs"></tab-widget>
        <editor-widget :mail-list="teachers"></editor-widget>
        <div class="pad-h-3"></div>

        <!--消息历史-->
        <h3 class="base-title-widget">消息历史</h3>
        <div class="basic-article-widget pad-w-2" v-if="history_msg.length > 0">
            <p class="ba-w-p" v-for="msg of history_msg">
                <a @click="to_chapter" :data-mission="msg.mission_id">【提醒】 {{msg.teacher}} 老师,点评了你所提交的作业</a>
                <span class="ba-w-p-right">{{msg.date_time}}</span>
            </p>
        </div>
        <div class="no-result-widget" v-else>
            <span class="icon-nodata nr-w-ico"></span>
            <span class="nr-w-text">暂无点评</span>
        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Editor from "@/components/widget/editor-widget";

    /*测试数据*/
    var teachers = [//【data:老师通讯录】
        {
            uid: "u1",
            state: "",
            name: "铁林",
            suffix: "导师",
        },
        {
            uid: "u2",
            state: "",
            name: "宗盛",
            suffix: "讲师",
        },
        {
            uid: "u3",
            state: "",
            name: "王刚",
            suffix: "班主任",
        },
    ], history_msg = [//【data:历史消息】
        {
            history_msg_id: "history_msg_1",
            mission_id: "1",
            teacher: "张大千",
            date_time: "2018-10-10 23:20"
        }
    ];

    export default {
        name: "teacher",
        data() {
            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "老师",
                        state: "act",
                        type: "route",
                        link: "/coffee/teacher",
                    },
                    {
                        name: "同班",
                        type: "route",
                        link: "/coffee/same_class",
                    },
                    {
                        name: "同学",
                        type: "route",
                        link: "/coffee/student",
                    },
                ],

                /*通讯老师*/
                teachers: [],

                /*老师点评消息*/
                history_msg: [],

            }
        },
        components: {
            "tab-widget": Tab,
            "editor-widget": Editor,
        },
        methods: {

            /*连接至课程章节*/
            to_chapter(e){
                this.$axios.get("chaptertask/")
                    .then( tasks => {
                        tasks.data.forEach( task => {
                            if(task.id == e.target.dataset.mission){
                                this.$router.push("/learn_detail?sub_c_id=" + task.chapter_name);
                                return;
                            }
                        })
                    })
            }

        },
        mounted() {
            this.$axios.all([
                this.$axios.get("user_class/"),
                this.$axios.get("evaluation/")
            ]).then(this.$axios.spread((r1, r2) => {

                /*初始化老师列表*/
                let teachers = r1.data;
                teachers.forEach(teacher => {
                    let th = teacher.teacher_head,       //导师
                        tl = teacher.teacher_lector,     //班主任
                        tt = teacher.teacher_tutor;      //讲师
                    this.teachers = [
                        {
                            uid: th.id,
                            state: "",
                            name: th.name,
                            suffix: "导师",
                        },
                        {
                            uid: tl.id,
                            state: "",
                            name: tl.name,
                            suffix: "班主任",
                        },
                        {
                            uid: tt.id,
                            state: "",
                            name: tt.name,
                            suffix: "讲师",
                        },
                    ]
                })

                /*初始化消息列表*/
                let msgs = r2.data;
                msgs.forEach(msg => {
                    if (msg.user === this.$store.state.user.uid) {
                        this.history_msg.push({
                            history_msg_id: msg.id,
                            mission_id: msg.mission,
                            teacher: msg.teacher.name,
                            date_time: msg.evaluation_time.replace("T", " ")
                        });
                    }
                });
                this.history_msg.reverse();

            }))
        },
    }
</script>

<style scoped>
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/basic-article-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/head-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>



